import React from 'react';
import { Box } from '@mui/material';

function App() {


	return (
		<Box
			display="flex"
			justifyContent={'center'}
			alignItems={'center'}
			sx={{ width: '100%', boxSizing: 'border-box', height: '50%' }}>
			Главная Страница
		</Box>
	);
}

export default App;
